<template>
  <div style="margin-left: 6%">
    <a-descriptions bordered title="咨询详情" :column="{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }">
      <a-descriptions-item label="标题" span="3">
        咨询标题
      </a-descriptions-item>
      <a-descriptions-item label="姓名">
        Cloud Database
      </a-descriptions-item>
      <a-descriptions-item label="性别">
        Prepaid
      </a-descriptions-item>
      <a-descriptions-item label="电话">
        18:00:00
      </a-descriptions-item>
      <a-descriptions-item label="地址" span="2">
        $80.00
      </a-descriptions-item>
      <a-descriptions-item label="咨询时间">
        $20.00
      </a-descriptions-item>
      <a-descriptions-item label="案件详情" span="3">
        Data disk type: MongoDB
        <br/>
        Database version: 3.4
        <br/>
        Package: dds.mongo.mid
        <br/>
        Storage space: 10 GB
        <br/>
        Replication factor: 3
        <br/>
        Region: East China 1<br/>
      </a-descriptions-item>
      <a-descriptions-item label="咨询诉求" span="3">
        Data disk type: MongoDB
        <br/>
        Database version: 3.4
        <br/>
        Package: dds.mongo.mid
        <br/>
        Storage space: 10 GB
        <br/>
        Replication factor: 3
        <br/>
        Region: East China 1<br/>
      </a-descriptions-item>
      <a-descriptions-item label="附件" span="3">
        <a href="#">附件</a>
      </a-descriptions-item>
    </a-descriptions>

    <div style="margin-top: 5%;">
      <a-divider>回复</a-divider>
    </div>

    <a-list
        class="comment-list"
        :header="`${data.length} replies`"
        item-layout="horizontal"
        :data-source="data"
    >
      <a-list-item slot="renderItem" slot-scope="item, index">
        <a-comment :author="item.author" :avatar="item.avatar">
          <template slot="actions">
            <span v-for="action in item.actions">{{ action }}</span>
          </template>

          <div slot="content">
            <p>
              {{ item.content }}
            </p>
            <a href="#">
              <a-icon type="folder" class="icon"/>
              <span> 附件</span></a>
          </div>
          <a-tooltip slot="datetime" :title="item.datetime.format('YYYY-MM-DD HH:mm:ss')">
            <span>{{ item.datetime.fromNow() }}</span>
          </a-tooltip>
        </a-comment>
      </a-list-item>
    </a-list>
    <div>
      <a-list
          v-if="comments.length"
          :data-source="comments"
          :header="`${comments.length} ${comments.length > 1 ? 'replies' : 'reply'}`"
          item-layout="horizontal"
      >
        <a-list-item slot="renderItem" slot-scope="item, index">
          <a-comment
              :author="item.author"
              :avatar="item.avatar"
              :content="item.content"
              :datetime="item.datetime"
          />
        </a-list-item>
      </a-list>
      <a-comment>
        <a-avatar
            slot="avatar"
            src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png"
            alt="Han Solo"
        />
        <div slot="content">
          <a-form-item>
            <a-textarea :rows="4" :value="value" @change="handleChange" />
          </a-form-item>
          <a-form-item>
            <a-upload-dragger
                name="file"
                :multiple="true"
                action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                @change="handleUpload"
            >
              <p class="ant-upload-drag-icon">
                <a-icon type="inbox" />
              </p>
              <p class="ant-upload-text">
                单击或拖动文件到此区域以上传
              </p>
              <p class="ant-upload-hint">
                支持单次或批量上传
              </p>
            </a-upload-dragger>
          </a-form-item>
          <a-form-item>
            <a-button html-type="submit" :loading="submitting" type="primary" @click="handleSubmit">
              回 复
            </a-button>
          </a-form-item>
        </div>
      </a-comment>
    </div>
  </div>
</template>

<script>
  import moment from 'moment';

  export default {
    name: 'ConsultationDetail',
    data() {
      return {
        data: [
          {
            actions: ['Reply to'],
            author: 'Han Solo',
            avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
            content:
                'We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.',
            datetime: moment().subtract(1, 'days'),
          },
          {
            actions: ['Reply to'],
            author: 'Han Solo',
            avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
            content:
                'We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.',
            datetime: moment().subtract(2, 'days'),
          },
        ],
        //真正的评论
        comments: [],
        submitting: false,
        value: '',
        moment,
      };
    },
    methods: {
      handleSubmit() {
        if (!this.value) {
          return;
        }

        this.submitting = true;

        setTimeout(() => {
          this.submitting = false;
          this.comments = [
            {
              author: 'Han Solo',
              avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
              content: this.value,
              datetime: moment().fromNow(),
            },
            ...this.comments,
          ];
          this.value = '';
        }, 1000);
      },
      handleChange(e) {
        this.value = e.target.value;
      },
      handleUpload(info) {
        const status = info.file.status;
        if (status !== 'uploading') {
          console.log(info.file, info.fileList);
        }
        if (status === 'done') {
          this.$message.success(`${info.file.name} file uploaded successfully.`);
        } else if (status === 'error') {
          this.$message.error(`${info.file.name} file upload failed.`);
        }
      },
    },
  }
</script>

<style scoped>
  .icon {
    display: inline-block;
    vertical-align: baseline;
  }
</style>